import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { AccountService } from 'app/shared/services/account/account.service';
import { Router } from '@angular/router';
@Component({
  selector: 'app-profile-overview',
  templateUrl: './overview.component.html',
})
export class ProfileOverviewComponent {
  
  localStorage : any;
  userInfo:any;
  selectedIndex=0;
  endUserTab = false;
  cmsUserTab = false;
  userObj: any;
  constructor(private snackBar: MatSnackBar,private accountService: AccountService,private router: Router) {

  }
  ngOnInit()
   {
    this.initialize();
    this.getUserInfo();
   }
   initialize(){
    this.localStorage = JSON.parse( localStorage.getItem('userDetails'));
   }
   getUserInfo(){
    this.accountService.getUserInfo(this.localStorage.userId).subscribe(
			(data:any)=>{
        this.userInfo = data;
        this.userObj = data;
        if(this.userObj[0].user_type == "END"){
          this.endUserTab = true;
          this.cmsUserTab = false;
        }else{
          this.endUserTab = false;
          this.cmsUserTab = true;
        }
      },(err)=>{}
		)
   }
   
}
