import { Component, OnInit, Inject, ElementRef, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators,FormControl } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MtxDialogData } from '@ng-matero/extensions';
import { AccountService } from 'app/shared/services/account/account.service';
@Component({
  selector: 'app-change-password',
  templateUrl: './change-password.component.html',
  styleUrls: ['./change-password.component.scss']
})
export class ChangePasswordComponent implements OnInit {

  params: any;
  isEditMode = false;
  form: FormGroup;
  localStorage : any;
  status = false;
  isPasswordFieldType: boolean;
  isPasswordConfirmFieldType: boolean;
  logs:any;
  @ViewChild("toggleElement") ref: any;
  @ViewChild("toggleAvailable") toggleAvailable: any;

  constructor(private fb: FormBuilder,private snackBar: MatSnackBar,private accountService: AccountService,
    public dialogRef: MatDialogRef<ChangePasswordComponent>, @Inject(MAT_DIALOG_DATA) public data: MtxDialogData) {}

  ngOnInit() {
    this.initialize();
  }

  initialize(){

    this.localStorage = JSON.parse( localStorage.getItem('userDetails'));

   // this.params = this.data;
    
    //this.isEditMode = this.params.action == 'CREATE' ? false : true;

    this.form = this.fb.group({
      currentPassword: ['' ],
      newPassword: ['', [Validators.required]],
      cnPassword: ['',[this.confirmValidator] ]
      
    });

   
   
  }
  confirmValidator = (control: FormControl): { [s: string]: boolean } => {
    if (!control.value) {
      return { error: true, required: true };
    } else if (control.value !== this.form.controls.password.value) {
      return { error: true, confirm: true };
    }
  }
  togglePasswordFieldType(){
    this.isPasswordFieldType = !this.isPasswordFieldType;
  }
  togglePasswordConfirmFieldType(){
    this.isPasswordConfirmFieldType = !this.isPasswordConfirmFieldType;
  }

  close() {
     this.dialogRef.close();
  }

  

  onYesClick(){
    //if(this.form.valid){
         
      let formValues = this.form.value;
      let formMobileValues = this.form.value;
      let params = {
        id:this.localStorage.userId,
        password: formValues.newPassword,
        registration_origin: "WEB",
        modify_by: this.localStorage.userId,
       
      }
      //
      this.accountService.updatePassword(params).subscribe((res:any)=>{
        //
        this.logs = res;
          this.snackBar.open(res.message, 'Dismiss', { duration: 3000 });
        
       },(err)=>{
        //this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 3000 });
      // this.router.navigateByUrl('/login');   
    })


    // }else{
    // //  this.snackBar.open(MessageConstant.validOTPError, 'Dismiss', { duration: 5000 });
    // }
    this.dialogRef.close();
  }

  getErrorMessage(form: FormGroup) {
    return form.get('email').hasError('required')
      ? 'You must enter a value'
      : form.get('email').hasError('email')
      ? 'Not a valid email'
      : '';
  }

  assignValues(params){
    
    this.form.get('currentPassword').setValue(params.data.currentPassword);
    this.form.get('newPassword').setValue(params.data.newPassword); 
    
  }


  
  
}





